      return {
        TrapIcon: MagicTarotCardsIcon,
        title: {
          pt: 'O Caminho Estendido',
          en: 'The Increased Path',
        },
        description: {
          pt: 'Nessa rodada, 7 portas esto disponveis a serem entradas.',
          en: 'This round 7 doors can be entered.',
        },
      };
